<template>
  <!-- <div>

         <mybutton @click="login">登录</mybutton>
  </div>-->
  <div id="app">
    <div class="container">
      <div class="close" @click="exitLogin">
        <span class="iconfont iconicon-test"></span>
      </div>
      <div class="logo">
        <span class="iconfont iconnew"></span>
      </div>
      <div class="inputs">
        <myinput data-v-744880be placeholder="请输入手机号" 
                 :rules='/^(\d{5,6})|^(1\d{10})$/'
                 msg='用户名或者手机号输入不正确'
                 @input="handleinput"
                 :value='users.username'>
        </myinput>
        <myinput data-v-744880be placeholder="密码"  type="password" 
                 :rules='/^\S{3,16}$/'
                 msg='请输入3-16位的密码'
                 v-model='users.password'>
        </myinput>
      </div>
      <p class="tips">
        没有账号？
        <a href="#/register" class>去注册</a>
      </p>
      <mybutton @click="login">登录</mybutton>
    </div>
  </div>
</template>

<script>
import mybutton from "@/components/mybutton.vue";
import myinput from '@/components/myinput.vue'
import {login} from '../apis/user.js'
export default {
  components: {
    mybutton,myinput
  },
  data(){
    return {
      users:{
          username:'10086',
          password:'123'
      }
    }
  },
  methods: {
    async login() {
      if(/^(d{5,6})|^(1\d{10})$/.test(this.users.username)||/^\S{3,16}$/.test(this.users.password)){
        let res = await login(this.users);
        //console.log(res)
        if(res.data.message === '用户不存在'){
          this.$toast.fail(res.data.message)
        }else{
          localStorage.setItem('login_token',res.data.data.token)
          localStorage.setItem('user',JSON.stringify(res.data.data.user))
          this.$toast.success('登录成功')
          this.$router.push({path:`/personal/${res.data.data.user.id}`})
        }
      }else{
        this.$toast.fail('用户输入不合法')
      }
    },
    exitLogin(){
        this.$router.push({name:'Index'})
    },
    handleinput(data){
      this.users.username = data;
    },
  }
};
</script>

<style lang='less' scoped>
    .container {
  padding: 20px;
}

.close {
  span {
    font-size: 27 / 360 * 100vw;
  }
}

.logo {
  display: flex;
  justify-content: center;

  span {
    display: block;
    font-size: 126 / 360 * 100vw;
    color: #d81e06;
  }
}

.inputs {
  input {
    margin-bottom: 20px;
  }
}

.tips {
  text-align: right;
  margin-bottom: 20px;

  a {
    color: #3385ff;
  }
}
</style>