<template>
  <div class="search">
    <div class="header">
      <span class="iconfont iconjiantou2" @click="$router.back()"></span>
      <van-search
        v-model="userkey"
        placeholder="请输入搜索关键词"
        shape="round"
      />
      <div @click="onSearch(userkey)">搜索</div>
    </div>
    <div class="historyList">
      <h2>历史记录</h2>
      <a href="javaScript:;" v-for="(item,index) in historyList" :key="index" @click="onSearch(item)">{{item}}</a>
    </div>
    <div class="historyList">
      <h2>搜索结果</h2>
      <router-link :to="{path:`/articleDetail/${item.id}`}" v-for="(item,index) in searchList" :key="index">{{item.title}}</router-link>
    </div>
  </div>
</template>

<script>
import {searchArticle} from '@/apis/article.js'
export default {
  data(){
    return {
      userkey:'',
      historyList:[],
      searchList:[]
    }
  },
  mounted(){
    this.historyList = this.getdata()
  },
  methods:{
    async onSearch(userkey){
      console.log(userkey);
      this.searchList.length = 0;
      let res = await searchArticle({
        keyword: userkey
      })
      console.log(res);
      if(res.data.data.length > 0){
        this.searchList = res.data.data;
      }
      this.historyList = this.getdata();
      let index = this.historyList.indexOf(userkey);
      if(index != -1){
        this.historyList.splice(index,1)
      }
      this.historyList.unshift(userkey);
      localStorage.setItem('search_history',JSON.stringify(this.historyList));
    },
    getdata(){
      return JSON.parse(localStorage.getItem('search_history')||'[]')
    }
  }
};
</script>

<style lang='less' scoped>
  .header{
    display: flex;
    height: 50px;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    border-bottom: 1px solid #ccc;
    .van-search{
      flex: 1;
      padding: 5px 12px;
    }

  }
  .historyList{
    padding: 10px;
    border-bottom: 1px solid #ccc;
    h2{
      line-height: 40px;
      font-weight: bold;
    }
    >a{
      //display: block;
      margin-right: 20px;
      background-color: #eee;
      border-radius: 5px;
    }
  }
</style>