<template>
  <div class="personal">
    <router-link :to="{path:`/editpersonal/${currentUser.id}`}">
      <div class="profile">
        <span slot="left" class="iconfont iconjiantou2" @click="$router.back()"></span>
        <!-- $axios.defaults.baseURL读取axios的服务器路径 -->
        <img :src="currentUser.head_img" alt />
        <div class="profile-center">
          <div class="name">
            <span class="iconfont iconxingbienan"></span>{{currentUser.nickname}}
          </div>
          <div class="time">{{currentUser.create_date}}</div>
        </div>
        <span class="iconfont iconjiantou1"></span>
      </div>
    </router-link>
    <mycell title="我的关注" desc='关注的用户' @click="$router.push({name:'Myfollow'})"></mycell>
    <mycell title="我的收藏" desc='文章/视频' @click="$router.push({name:'Mystar'})"></mycell>
    <mycell title="我的跟帖" desc='跟帖/回复' @click="setting"></mycell>
    <!-- <mycell title="设置" @click="setting"></mycell> -->
    <mybutton class="btn" @click="exit">退出</mybutton>
  </div>
</template>

<script>
import mycell from '@/components/mycell.vue'
import mybutton from '@/components/mybutton.vue'
import {getUserById} from '../apis/user.js'
import moment from 'moment'
import {baseURL} from '@/utils/myaxios'
export default {
  components:{
    mycell,mybutton
  },
  data(){
    return {
      currentUser:{}
    }
  },
  async mounted(){
    //console.log(this.$route.params.id);
    let id = this.$route.params.id;
    let res = await getUserById(id);
    console.log(res);
    if(res.data.message === '获取成功'){
      this.currentUser = res.data.data;
      this.currentUser.head_img = baseURL+this.currentUser.head_img;
      this.currentUser.create_date = moment(this.currentUser.create_date).format('YYYY-MM-DD hh:mm:ss'); 
    }
  },
  methods:{
    // 退出登录
    exit(){
      localStorage.removeItem('login_token');
      this.$router.push({name:'Index'});
    },
    // 设置
    setting(){
      this.$dialog.alert({
      message: '暂时没有这个功能哟'
    });
    }
  }
}
</script>

<style lang='less' scoped>
.personal{
    width: 100vw;
    height: 100vh;
    background-color: #fff;
}
a{
    color: #666;
}
.profile {
  display: flex;
  padding: 20px 10px;
  justify-content: space-between;
  align-items: center;
  border-bottom: 5px #ddd solid;
  .iconjiantou2{
    margin-right: 20px;
  }
  img {
    width: 70 / 360 * 100vw;
    height: 70 / 360 * 100vw;
    border-radius: 50%;
  }

  .profile-center {
    flex: 1;
    padding: 0 10px;
  }

  .name {
    span {
      color: #75b9eb;
    }
  }

  .time {
    color: #666;
    font-size: 14px;
    margin-top: 5px;
  }
  
}
.btn {
    margin:  20px auto;
    background-color: #f00;
  }
</style>
