<template>
  <div class="edit">
      <myheader title="编辑个人信息">
          <span class="iconfont iconjiantou2" slot="left" @click="$router.back()"></span>
          <span slot="right" @click="$router.push({path:'/'})"><van-icon name="wap-home-o" /></span>
      </myheader>
      <div class="userimg">
          <img :src="currentUser.head_img" alt="">
          <van-uploader :after-read="afterRead"></van-uploader>
      </div>
      <mycell title="昵称" :desc='currentUser.nickname' @click="nickshow=!nickshow"></mycell>
      <van-dialog v-model="nickshow" title="修改昵称" show-cancel-button @confirm='updateNickname'>
          <van-field ref='nick' :value='currentUser.nickname' placeholder='请输入昵称' required label='昵称'></van-field>
      </van-dialog>
      <mycell title="密码" :desc='currentUser.password' type='password' @click="passshow=!passshow"></mycell>
      <van-dialog v-model="passshow" title="修改密码" show-cancel-button @confirm='updatePassword'>
          <van-field ref='originPass'  placeholder='请输入原密码' required label='原密码' type="password"></van-field>
          <van-field ref='newPass'  placeholder='请输入新密码' required label='新密码'></van-field>
      </van-dialog>
      <mycell title="性别" :desc='currentUser.gender === 0?"女":"男"' @click="gendershow=!gendershow"></mycell>
      <van-dialog v-model="gendershow" show-cancel-button @confirm='updateGender'>
          <van-picker :columns="['女','男']" @change="onchange" :default-index="currentUser.gender"></van-picker>
      </van-dialog>
  </div>
</template>

<script>
import myheader from '@/components/myheader.vue'
import mycell from '@/components/mycell.vue'
import {getUserById,updateUserById} from '@/apis/user.js'
import {uploadFile} from '@/apis/upload.js'
export default {
    components:{
        myheader,mycell
    },
    data(){
        return {
            currentUser:{},
            nickshow:false,
            passshow:false,
            gendershow:false
        }
    },
    async mounted(){
        let res = await getUserById(this.$route.params.id);
        //console.log(res)
        this.currentUser = res.data.data;
        this.currentUser.head_img = 'http://127.0.0.1:3000'+res.data.data.head_img;
    },
    methods:{
        async afterRead(file){
            let formdata = new FormData();
            formdata.append('file',file.file)
            let res = await uploadFile(formdata);
            if(res.data.message === '文件上传成功'){
                this.currentUser.head_img = 'http://127.0.0.1:3000'+res.data.data.url;
                let res2 = await updateUserById(this.currentUser.id,{head_img:res.data.data.url})
                console.log(res2)
                if(res2.data.message === '修改成功'){
                    this.$toast.success('修改成功')
                }else{
                    this.$toast.fail('修改失败')
                }
            }else{
                this.$toast.fail('文件上传失败')
            }
        },
        async updateNickname(){
            let name = this.$refs.nick.$refs.input.value;
            let res = await updateUserById(this.currentUser.id,{
                nickname:name
            });
            if(res.data.message == '修改成功'){
                this.currentUser.nickname = name;
                this.$toast.success('修改成功')
            }else{
                this.$toast.fail('修改失败')
            }
        },
        async updatePassword(){
            let originPass = this.$refs.originPass.$refs.input.value;
            if(originPass === this.currentUser.password){
                let newPass = this.$refs.newPass.$refs.input.value;
                let res = await updateUserById(this.currentUser.id,{
                    password:newPass
                })
                //console.log(res)
                //if(res.data.message === '修改成功'){
                    this.$toast.success(res.data.message)
                //}
            }
            
        },
        async updateGender(){
            let res = await updateUserById(this.currentUser.id,{
                gender: this.gender
            })
            this.$toast(res.data.message);
            if(res.data.message === '修改成功'){
                this.currentUser.gender = this.gender;
            }
        },
        onchange(picker,value,index){
            console.log(this.currentUser.gender);
            this.$toast(`当前值：${value},当前索引：${index}`);
            this.gender = index;
        }
    }
};
</script>

<style lang='less' scoped>
    .userimg{
        height: 150px;
        position: relative;
        img {
            display: block;
            width: 90 / 360 * 100vw;
            height: 90 / 360 * 100vw;
            border-radius: 50%;
            position: absolute;
            left: 50%;
            top: 50%;
            margin-top: 20px;
            transform: translate(-50%,-50%)
        }
    }
  /deep/.van-uploader__upload {
    width: 90 / 360 * 100vw;
    height: 90 / 360 * 100vw;
  }
  /deep/.van-uploader{
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%,-50%);
      opacity: 0;
      margin-top: 20px;
  }
</style>