<template>
  <div class="comment">
    <myheader title="评论详情">
      <span slot="left" class="iconfont iconjiantou2" @click="$router.back()"></span>
    </myheader>
    <div class="lists">
      <div class="item" v-for="comment in commentList" :key="comment.id">
        <div class="head">
          <img :src="comment.user.head_img" alt />
          <div>
            <p>{{comment.user.nickname}}</p>
            <span>2小时前</span>
          </div>
          <span @click="sendComment(comment)">回复</span>
        </div>
        <commentItem v-if="comment.parent" :parent="comment.parent" @postComment='sendComment'></commentItem>
        <div class="text">{{comment.content}}</div>
      </div>
    </div>
    <myCommentFooter :post="article" @refresh="refresh" :obj='replyObj' @reset='replyObj=null'></myCommentFooter>
  </div>
</template>

<script>
import { getComment, getArticleById } from "../apis/article.js";
import myheader from "@/components/myheader.vue";
import commentItem from "../components/myCommentItem.vue";
import myCommentFooter from "../components/myCommentFooter.vue";
import {baseURL} from '@/utils/myaxios'
export default {
  components: {
    myheader,
    commentItem,
    myCommentFooter
  },
  data() {
    return {
      commentList: [],
      article: {},
      replyObj: null
    };
  },
  async mounted() {
    this.init();
    let res2 = await getArticleById(this.$route.params.id);
    this.article = res2.data.data;
  },
  methods: {
    async init() {
      let res = await getComment(this.$route.params.id, {
        pageSize: 40,
        pageIndex: 1
      });
      this.commentList = res.data.data;
      for (var i = 0; i < this.commentList.length; i++) {
        if (this.commentList[i].user.head_img.indexOf("http") === -1) {
          this.commentList[i].user.head_img =
            baseURL + this.commentList[i].user.head_img;
        }
      }
    },
    refresh(){
        this.init();
        window.scrollTo(0,0)
    },
    sendComment(comment){
        this.replyObj = comment
    }
  }
};
</script>

<style lang='less' scoped>
.lists {
  border-top: 5px solid #ddd;
  padding: 0 15px;
  .item {
    padding: 10px 0;
    border-bottom: 1px solid #ccc;
    .head {
      display: flex;
      justify-content: space-between;
      align-items: center;
      > img {
        width: 50/360 * 100vw;
        height: 50/360 * 100vw;
        display: block;
        border-radius: 50%;
      }
      > div {
        flex: 1;
        display: flex;
        flex-direction: column;
        margin-left: 10px;
        > span {
          font-size: 12px;
          color: #999;
          line-height: 25px;
        }
      }
      > span {
        color: #999;
        font-size: 13px;
      }
    }
    .text {
      font-size: 14px;
      color: #333;
      padding: 20px 0 10px 0;
    }
  }
}
</style>