<template>
  <div class="index" @click="handleClick">
    <div class="header">
      <div class="logo">
        <span class="iconfont iconnew"></span>
      </div>
      <div class="search" @click="$router.push({name:'Search'})">
        <van-icon name="search" />
        <span>搜索新闻</span>
      </div>
      <div class="user" @click="$router.push({path:`/personal/${id}`})">
        <van-icon name="manager" />
      </div>
    </div>
    <div class="categary">
      <van-tabs v-model="active" swipeable sticky>
        <van-tab :title="cate.name" v-for="cate in catelist" :key="cate.id">
          <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
            <!-- <myarticleBlock :post="item" v-for="item in cate.postlist" :key="item.id"></myarticleBlock> -->
            <van-list
              v-model="cate.loading"
              @load="onLoad"
              :finished="cate.finished"
              finished-text="没有更多了"
              :offset="10"
              :immediate-check="false"
            >
              <myarticleBlock
                :post="item"
                v-for="item in cate.postlist"
                :key="item.id"
                @click="$router.push({path: `/articleDetail/${item.id}`})"
              ></myarticleBlock>
            </van-list>
          </van-pull-refresh>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
import { getCategaries } from "@/apis/cate.js";
import { getArticle} from "@/apis/article.js";
import myarticleBlock from "../components/myarticleBlock.vue";
import {baseURL} from '@/utils/myaxios'
export default {
  components: {
    myarticleBlock
  },
  watch: {
    active() {
      if (this.catelist[this.active].postlist.length === 0) {
        //console.log(this.active);
        this.init();
      }
    }
  },
  data() {
    return {
      id: "",
      catelist: [],
      active: localStorage.getItem("login_token") ? 1 : 0,
      articleList: [],
      count: 0,
      isLoading: false
    };
  },
  async mounted() {
    
    //判断栏目是否设置过
    if (localStorage.getItem("cateList")) {
      this.catelist = JSON.parse(localStorage.getItem("cateList"));
      if(localStorage.getItem("login_token")){
        this.catelist.unshift(...[{id:0,name:'关注',is_top:1},{id:999,name:'头条',is_top:1}])
      }else{
        this.catelist.unshift(...[{id:999,name:'头条',is_top:1}])
      }
    } else {
      let res = await getCategaries();
      console.log(res);
      this.catelist = res.data.data;
    }
    this.id = JSON.parse(localStorage.getItem("user") || "{}").id;
    // let res2 = await getArticle()
    // console.log(res2)
    this.catelist = this.catelist.map(value => {
      return {
        ...value,
        postlist: [],
        pageSize: 5,
        pageIndex: 1,
        loading: false,
        finished: false
      };
    });
    this.init();
  },
  methods: {
    async init() {
      let res2 = await getArticle({
        pageSize: this.catelist[this.active].pageSize,
        pageIndex: this.catelist[this.active].pageIndex,
        category: this.catelist[this.active].id
      });
      this.catelist[this.active].loading = false;
      if (res2.data.data.length < this.catelist[this.active].pageSize) {
        this.catelist[this.active].finished = true;
      }
      //console.log(res2);
      for (var i = 0; i < res2.data.data.length; i++) {
        for (var j = 0; j < res2.data.data[i].cover.length; j++) {
          if (res2.data.data[i].cover[j].url.indexOf("http") == -1) {
            res2.data.data[i].cover[j].url =
              baseURL + res2.data.data[i].cover[j].url;
          }
        }
      }
      this.catelist[this.active].postlist.push(...res2.data.data);
      //console.log(this.catelist[this.active].postlist);
    },
    onRefresh() {
      this.catelist[this.active].pageIndex = 1;
      this.catelist[this.active].postlist.length = 0;
      setTimeout(() => {
        this.init();
        this.$toast.success("刷新成功");
        this.isLoading = false;
      }, 3000);
      this.catelist[this.active].finished = false;
    },
    onLoad() {
      this.catelist[this.active].pageIndex++;
      setTimeout(() => {
        this.init();
      }, 2000);
    },
    handleClick(e){
      console.log()
      if(e.target.className === 'van-sticky'){
        this.$router.push({name:'CateManager'})
      }
      
    }
  }
};
</script>

<style lang='less' scoped>
.header {
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: space-between;
  background-color: red;
  align-items: center;
  color: #fff;
  .logo {
    padding: 0 10px;
    .iconnew {
      font-size: 60px;
    }
  }
  .search {
    height: 40px;
    border-radius: 20px;
    background-color: rgba(255, 255, 255, 0.4);
    flex: 1;
    text-align: center;
    line-height: 40px;
  }
  .user {
    font-size: 25px;
    padding: 0 10px;
  }
}
/deep/.van-sticky{
  padding-right: 50px;
  &::after {
    content: "+";
    position: absolute;
    width: 51px;
    height: 44px;
    background-color: #fff;
    top: 0;
    right: 0;
    text-align: center;
    line-height: 42px;
    font-size: 35px;
  }
}
</style>