<template>
  <!-- <div>

         <mybutton @click="login">登录</mybutton>
  </div>-->
  <div id="app">
    <div class="container">
      <div class="close" @click="exitLogin">
        <span class="iconfont iconicon-test"></span>
      </div>
      <div class="logo">
        <span class="iconfont iconnew"></span>
      </div>
      <div class="inputs">
        <myinput data-v-744880be placeholder="请输入手机号" 
                 :rules='/^(\d{5,6})|^(1\d{10})$/'
                 msg='用户名或者手机号输入不正确'
                 @input="handleinput"
                 :value='users.username'>
        </myinput>
        <myinput data-v-744880be placeholder="密码"  type="password" 
                 :rules='/^\S{3,16}$/'
                 msg='请输入3-16位的密码'
                 v-model='users.password'>
        </myinput>
        <myinput data-v-744880be placeholder="昵称"  
                 :rules='/^\w+$/'
                 msg='请输入正确的昵称'
                 v-model='users.nikename'>
        </myinput>
      </div>
      <p class="tips">
        已注册？
        <a href="#/login" class>去登录</a>
      </p>
      <mybutton @click="register">注册</mybutton>
    </div>
  </div>
</template>

<script>
import mybutton from "@/components/mybutton.vue";
import myinput from '@/components/myinput.vue'
import {registerUsers} from '../apis/user.js'
export default {
  components: {
    mybutton,myinput
  },
  data(){
    return {
      users:{
          username:'',
          password:'',
          nickname:''
      }
    }
  },
  methods: {
    async register(){
      let res = await registerUsers({
        username:this.users.username,
        password:this.users.password,
        nickname:this.users.nickname
      })
      console.log(res);
      this.$toast(res.data.message);
      this.$router.push({name:'login'})
    },
    exitLogin(){
        this.$router.push({name:'Index'})
    },
    handleinput(data){
      this.users.username = data;
    }
  }
};
</script>

<style lang='less' scoped>
    .container {
  padding: 20px;
}

.close {
  span {
    font-size: 27 / 360 * 100vw;
  }
}

.logo {
  display: flex;
  justify-content: center;

  span {
    display: block;
    font-size: 126 / 360 * 100vw;
    color: #d81e06;
  }
}

.inputs {
  input {
    margin-bottom: 20px;
  }
}

.tips {
  text-align: right;
  margin-bottom: 20px;

  a {
    color: #3385ff;
  }
}
</style>