<template>
  <div class="btn" @click="btnclick">
      <span :class="icon"></span>
      <slot></slot>
  </div>
</template>

<script>
export default {
  props:['icon'],
  methods:{
    btnclick(event){
      this.$emit('click',event)
    }
  }
}
</script>

<style lang='less' scoped>
  .btn{
    width: 318*100vm/360;;
    height: 50px;
    background-color: rgb(70, 158, 12);
    border-radius: 25px;
    color: white;
    text-align: center;
    line-height: 50px;
    font-weight: bold;
    font-size: 20px;
  }
</style>