<template>
  <div class="articaldetail">
    <div class="header">
      <div class="left">
        <van-icon name="arrow-left back" @click="$router.back()" />
        <span class="iconfont iconnew new"></span>
      </div>
      <span
        :class="{active:articleList.has_follow}"
        @click="follow"
      >{{articleList.has_follow?'取关':'关注'}}</span>
    </div>
    <div class="detail">
      <div class="title">{{articleList.title}}</div>
      <div class="desc">
        <span>新闻来源：{{articleList.user.nickname}}</span> &nbsp;&nbsp;
        <span>{{articleList.create_date}}</span>
      </div>
      <div class="content" v-if="articleList.type === 1" v-html="articleList.content"></div>
      <video :src="articleList.content" v-if="articleList.type === 2" controls></video>
      <div class="opt">
        <span class="like" :class="{active:articleList.has_like}" @click="like">
          <van-icon name="good-job-o" />点赞
        </span>
        <span class="chat">
          <van-icon name="chat" class="w" />微信
        </span>
      </div>
    </div>
    <!-- 精彩跟帖 -->
    <div class="keeps">
      <h2>精彩跟帖</h2>
      <!-- <div class="item">
        <div class="head">
          <img src="../assets/logo.png" alt />
          <div>
            <p>火星网友</p>
            <span>2小时前</span>
          </div>
          <span>回复</span>
        </div>
        <div class="text">文章说得很有道理</div>
      </div>
      <div class="more">更多跟帖</div> -->
      <div class="lists">
        <div class="item" v-for="comment in commentList" :key="comment.id">
          <div class="head">
            <img :src="comment.user.head_img" alt />
            <div>
              <p>{{comment.user.nickname}}</p>
              <span>2小时前</span>
            </div>
            <span @click="sendComment(comment)">回复</span>
          </div>
          <commentItem v-if="comment.parent" :parent="comment.parent" @postComment="sendComment"></commentItem>
          <div class="text">{{comment.content}}</div>
        </div>
      </div>
    </div>
    <myCommentFooter :post="articleList"></myCommentFooter>
  </div>
</template>

<script>
import { getArticleById } from "@/apis/article.js";
import { userFollows, userUnFollows } from "../apis/user.js";
import { postLike } from "@/apis/article.js";
import myCommentFooter from "@/components/myCommentFooter.vue";
import commentItem from "../components/myCommentItem";
import { getComment } from "@/apis/article.js";
import moment from 'moment';
import {baseURL} from '@/utils/myaxios'
export default {
  components: {
    myCommentFooter,
    commentItem
  },
  data() {
    return {
      commentList: [],
      articleList: {
        user: {}
      }
    };
  },
  async mounted() {
    // 根据id获取文章的详情，实现文章详情的动态渲染
    let res = await getArticleById(this.$route.params.id);
    res.data.data.create_date = moment(res.data.data.create_date).format('YYYY-MM-DD hh:mm:ss'); 
    console.log(res.data.data);
    this.articleList = res.data.data;
    let res2 = await getComment(this.$route.params.id, {
      pageSize: 40,
      pageIndex: 1
    });
    this.commentList = res2.data.data;
    for (var i = 0; i < this.commentList.length; i++) {
      if (this.commentList[i].user.head_img.indexOf("http") === -1) {
        this.commentList[i].user.head_img =
          baseURL + this.commentList[i].user.head_img;
      }
    }
  },
  methods: {
    async follow() {
      if (this.articleList.has_follow) {
        let res = await userUnFollows(this.articleList.user.id);
        //console.log(res)
        if (res.data.message === "取消关注成功") {
          this.$toast.success(res.data.message);
        } else {
          this.$toast.fail(res.data.message);
        }
        this.articleList.has_follow = false;
      } else {
        let res = await userFollows(this.articleList.user.id);
        //console.log(res)
        if (res.data.message === "关注成功") {
          this.$toast.success(res.data.message);
        } else {
          this.$toast.fail(res.data.message);
        }
        this.articleList.has_follow = true;
      }
    },
    async like() {
      let res = await postLike(this.$route.params.id);
      //console.log(res);
      if (res.data.message === "点赞成功") {
        this.articleList.has_like = true;
        this.$toast.success(res.data.message);
      } else {
        this.$toast.success(res.data.message);
        this.articleList.has_like = false;
      }
    }
  }
};
</script>

<style lang='less' scoped>
.articaldetail {
  padding-bottom: 50px;
}
.active {
  background-color: #f00;
  color: #fff;
}
.header {
  padding: 0px 10px;
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #ccc;
  > .left {
    height: 100%;
    vertical-align: middle;
    position: relative;
    .van-icon {
      position: absolute;
      top: 15px;
      left: 0;
    }
    > span {
      margin-left: 22px;
      font-size: 50px;
    }
  }
  > span {
    border: 1px solid #eee;
    padding: 5px 15px;
    text-align: center;
    border-radius: 15px;
    font-size: 13px;
  }
}
.detail {
  padding: 15px;
  .title {
    font-size: 18px;
    font-weight: bold;
    padding: 10px 0;
  }
  .desc {
    line-height: 30px;
    color: #999;
    font-size: 13px;
  }
  .content {
    text-indent: 2em;
    line-height: 24px;
    font-size: 15px;
    padding-bottom: 30px;
    width: 100%;
    /deep/img {
      display: block;
      width: 100%;
    }
    /deep/.photo {
      img {
        display: block;
        width: 100%;
      }
    }
  }
}
.opt {
  display: flex;
  justify-content: space-around;
  .like,
  .chat {
    height: 25px;
    padding: 0 15px;
    font-size: 14px;
    line-height: 25px;
    text-align: center;
    border: 1px solid #ccc;
    border-radius: 15px;
  }
  .w {
    color: rgb(84, 163, 5);
  }
}
.keeps {
  border-top: 5px solid #ddd;
  padding: 0 15px;
  > h2 {
    line-height: 50px;
    text-align: center;
  }
  .item {
    padding: 10px 0;
    border-bottom: 1px solid #ccc;
    .head {
      display: flex;
      justify-content: space-between;
      align-items: center;
      > img {
        width: 50/360 * 100vw;
        height: 50/360 * 100vw;
        display: block;
        border-radius: 50%;
      }
      > div {
        flex: 1;
        display: flex;
        flex-direction: column;
        margin-left: 10px;
        > span {
          font-size: 12px;
          color: #999;
          line-height: 25px;
        }
      }
      > span {
        color: #999;
        font-size: 13px;
      }
    }
    .text {
      font-size: 14px;
      color: #333;
      padding: 20px 0 10px 0;
    }
  }
  .more {
    width: 100px;
    height: 30px;
    line-height: 30px;
    text-align: center;
    border: 1px solid #ccc;
    border-radius: 15px;
    margin: 20px auto;
    font-size: 13px;
  }
}
video {
  width: 100%;
}
</style>

