import axios from '@/utils/myaxios.js'
export const getArticle = (params) => axios({
    url: '/post',
    params
})
export const getArticleById = (id) => axios({
    url: `/post/${id}`
})

export const postLike = (id) => axios({
    url: `/post_like/${id}`
})
export const getComment = (id,params) => axios({
    url: `/post_comment/${id}`,
    params
})
export const searchArticle = (params) => axios({
    url: `/post_search`,
    params
})
export const getUserStars =()=>{
    return axios({
        url:`/user_star`
    })
}
export const getList = (params) => {
    return axios({
        url:"queryList.json",
        params,
        dataType: "json",
    })
}

