<template>
  <div class="cate">
    <myheader title="频道管理">
      <span class="iconfont iconjiantou2" slot="left" @click="$router.back()"></span>
    </myheader>
    <div class="mycate">
      <p>点击删除频道</p>
      <span v-for="(item,index) in cateList" :key="index" @click="add(index)">{{item.name}}</span>
    </div>
    <div class="mycate">
      <p>点击增加频道</p>
      <span v-for="(item,index) in uncateList" :key="index" @click="del(index)">{{item.name}}</span>
    </div>
  </div>
</template>

<script>
import myheader from "@/components/myheader.vue";
import { getCategaries } from "@/apis/cate.js";
export default {
  data() {
    return {
      cateList: [],
      uncateList: []
    };
  },
  components: {
    myheader
  },
  async mounted() {
    if (localStorage.getItem("cateList")) {
      this.cateList = JSON.parse(localStorage.getItem("cateList"));
      this.uncateList = JSON.parse(localStorage.getItem("uncateList"));
    } else {
      let res = await getCategaries();
      console.log(res);
      this.cateList = res.data.data;
      if (localStorage.getItem("login_token")) {
        this.cateList = this.cateList.splice(2);
      } else {
        this.cateList = this.cateList.splice(1);
      }
      console.log(this.cateList);
    }
  },
  methods: {
    add(index) {
      this.uncateList.unshift(this.cateList[index]);
      this.cateList.splice(index, 1);
      localStorage.setItem("cateList", JSON.stringify(this.cateList));
      localStorage.setItem("uncateList", JSON.stringify(this.uncateList));
    },
    del(index) {
      this.cateList.push(this.uncateList[index]);
      this.uncateList.splice(index, 1);
      localStorage.setItem("uncateList", JSON.stringify(this.uncateList));
      localStorage.setItem("cateList", JSON.stringify(this.cateList));
    }
  }
};
</script>

<style lang='less' scoped>
.mycate {
  padding: 15px;
  clear: both;
  > p {
    font-size: 16px;
    color: #888;
    line-height: 30px;
  }
  > span {
    float: left;
    padding: 10px 20px;
    border: 1px solid #ccc;
    margin-left: 12px;
    margin-top: 10px;
    font-size: 14px;
  }
}
</style>