<template>
  <div class="comment">
    <div class="addcomment" v-show='!isFocus'>
      <input type="text" placeholder="写跟帖" @focus="handlerFocus" />
      <span class="comment" @click="$router.push({path:`/comment/${$route.params.id}`})">
        <i class="iconfont iconpinglun-"></i>
        <em>{{post.comment_length}}</em>
      </span>
      <i class="iconfont iconshoucang" @click="shoucang" :class="{shoucang:post.has_star}"></i>
      <i class="iconfont iconfenxiang"></i>
    </div>
    <div class="inputcomment" v-show='isFocus'>
        <textarea  ref='commtext' rows="5"></textarea>
        <div>
            <span class="send" @click="sendComment">发送</span>
            <span class="quxiao" @click="cancel">取消</span>
        </div>
    </div>
  </div>
</template>

<script>
import {getComment} from '@/apis/article.js'
import {userStar,postComment} from '@/apis/user.js'
export default {
  props:['post','obj'],
  data () {
    return {
      isFocus: false,
      commentList:[]
    }
  },
  watch:{
    obj(){
      console.log(this.obj);
      if(this.obj){
        this.isFocus = true;
      }
    }
  },
  methods: {
    //   获取焦点时触发
   
    handlerFocus () {
      this.isFocus = !this.isFocus
      setTimeout(()=>{
        this.$refs.commtext.focus()
      },10)
    },
    async shoucang(){
      let res = await userStar(this.$route.params.id);
      console.log(res);
      if(res.data.message === '收藏成功'){
        this.post.has_star = true;
        this.$toast.success(res.data.message)
      }else if(res.data.message == '取消成功'){
        this.post.has_star = false;
        this.$toast.success(res.data.message)
      }
    },
    async sendComment(){
      //console.log(2342)
      let data = {
        content:this.$refs.commtext.value
      }
      if(this.obj){
        data.parent_id = this.obj.id;
      }
      console.log(this.post.id);
      let res = await postComment(this.post.id,data);
      console.log(res)
      if(res.data.message === '评论发布成功'){
        this.$toast.success(res.data.message)
      }else{
        this.$toast.fail(res.data.message)
      }
      this.$refs.commtext.value = ''
      this.$emit('refresh');
      this.isFocus = false;
    },
    cancel(){
      //console.log(23123);
      this.isFocus = false;
      this.$emit('reset')
    }
  },
  async mounted(){
    //console.log(this.post)
    let res = await getComment(this.$route.params.id);
    //console.log(res);
    this.commentList = res.data.data;
  }
}
</script>

<style lang='less' scoped>
.shoucang{
  color: yellow;
}
.comment{
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100vw;
}
.inputcomment{
    padding: 10px;
    box-sizing: border-box;
    width: 100%;
    display: flex;
    background-color: #fff;
    align-items: flex-end;
    //position: relative;
    textarea{
        flex: 3;
        background-color: #eee;
        border:none;
        border-radius: 10px;
        padding: 10px;
    }
    div{
        padding: 20px;
    }
    span {
        display: block;
        flex: 1;
        height: 24px;
        line-height: 24px;
        padding: 0 10px;
        background-color: #f00;
        color:#fff;
        text-align: center;
        border-radius: 6px;
        font-size: 13px;
    }
    .send{
      position: absolute;
      //left: 0;
      top: 10px;
      background-color: green;
    }
    .quxiao{
     position: relative;
     bottom: -20px;
    }
}
.addcomment {
  background-color: #fff;
  width: 100%;
  box-sizing: border-box;
  padding: 10px;
  margin-top: 20px;
  display: flex;
  text-align: center;
  position: absolute;
  bottom: 0;
  left: 0;
  > input {
    flex: 4;
    height: 30px;
    line-height: 30px;
    border-radius: 15px;
    border: none;
    background-color: #eee;
    padding-left: 20px;
    font-size: 14px;
  }
  i {
    font-size: 20px;
  }
  > span {
    flex: 1;
    position: relative;
    > em {
      position: absolute;
      right: 0;
      top: -5px;
      font-size: 10px;
      background-color: #f00;
      color: #fff;
      border-radius: 5px;
      padding: 3px 5px;
    }
  }
  > i {
    flex: 1;
  }
}
</style>
