import axios from '@/utils/myaxios.js'
export const login = (data)=>{
    return axios({
        method:'post',
        url:'/login',
        data
    })
}
export const getUserById = (id)=>{
    return axios({
        url:`/user/${id}`
    })
}
export const updateUserById = (id,data)=>{
    return axios({
        method:'post',
        url:`/user_update/${id}`,
        data
    })
}

export const registerUsers = (data)=>{
    return axios({
        method:'post',
        url:`/register`,
        data
    })
}

export const userUnFollows = (id)=>{
    return axios({
        url:`/user_unfollow/${id}`
    })
}

export const userFollows = (id)=>{
    return axios({
        url:`/user_follows/${id}`
    })
}
export const userStar = (id)=>{
    return axios({
        url:`/post_star/${id}`
    })
}
export const postComment = (id,data) => axios({
    method:'post',
    url: `/post_comment/${id}`,
    data
})
export const unfollowUser = (id)=>{
    return axios({
        url:`/user_unfollow/${id}`
    })
}
export const getUserFollows = ()=>{
    return axios({
        url:`/user_follows`
    })
}
